import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux';

import { BsSearch } from 'react-icons/bs';
import { AiOutlineClose } from 'react-icons/ai';

import styles from '../../css/AddressModal.module.css';
import AddressList from './AddressList';
import { getAddress } from '../../store/addressSlice';

const AddressModal = ({ onClose, setInputs, toggleFocus }) => {
  const { addresses } = ({ addresses }) => addresses;
  console.log(addresses);
  const dispatch = useDispatch();
  const [isSearching, setIsSearching] = useState(false);
  const [keyword, setKeyword] = useState('');

  const onChange = (e) => {
    setKeyword(e.target.value);
  };

  const onSearch = async (e) => {
    if (e.key !== 'Enter') return;

    dispatch(getAddress(keyword));

    setIsSearching(true);
  };

  useEffect(() => {
    if (keyword === '') setIsSearching(false);
  }, [keyword]);

  return (
    <div className={styles.ModalContainer}>
      <div className={styles.Toolbar}>
        <span className={styles.HeaderTitle}> </span>
        <AiOutlineClose className={styles.CloseIcon} onClick={onClose} />
      </div>
      <div className={styles.Box}>
        <div className={styles.SearchContainer}>
          <BsSearch className={styles.SearchIcon} />
          <input
            type="text"
            placeholder="   "
            className={styles.SearchInput}
            value={keyword}
            onKeyUp={onSearch}
            onChange={onChange}
          />
        </div>
      </div>
      <>
        {isSearching ? (
          <AddressList
            keyword={keyword}
            setInputs={setInputs}
            toggleFocus={toggleFocus}
          />
        ) : (
          <div className={styles.DescriptionContainer}>
            <span className={styles.Description}>
                      
              . <br />
              ) , 432
            </span>
          </div>
        )}
      </>
      <div className={styles.Footer}>
        <span className={styles.FooterDescription}>
                .  
             .
          <br />  2016 8 1 5   .
           + 5      
           .
        </span>
      </div>
    </div>
  );
};

AddressModal.propTypes = {
  onClose: PropTypes.func.isRequired,
  setInputs: PropTypes.func.isRequired,
  toggleFocus: PropTypes.func.isRequired,
};

export default AddressModal;
