import React from 'react';
import styles from '../css/Schedule.module.css';
import CalendarImg from './icons/Calendar.png';
import ArrowImg from './icons/Arrow.png';
import ArrowLfetImg from './icons/ArrowLfet.png';

const Schedule = () => {
  return (
    <section className={styles.schedule_container}>
      <div className={styles.schedule_header}>
        <img className={styles.previousArrow} src={ArrowLfetImg} />
         
      </div>
      <div className={styles.schedule_main}>
        <div className={styles.procedure}>
            <span>2/4</span>
        </div>
        <div className={styles.title}>  </div>
        <div className={styles.date_container}>
          <div className={styles.day_container}>
            <div className={styles.dayTitle}></div>
            <div className={styles.day}>
              <div className={styles.selectDay}> </div>
              <img src={CalendarImg} />
            </div>
          </div>
          <div className={styles.day_container}>
            <div className={styles.dayTitle}></div>
            <div className={styles.day}>
              <div className={styles.selectDay}> </div>
              <img src={CalendarImg} />
            </div>
          </div>
        </div>

        <div className={styles.timeTitle}> </div>
        <div className={styles.time}>
          <div className={styles.selectTime}></div>
          <img src={ArrowImg} />
        </div>

        <div className={styles.timeTitle}> </div>
        <div className={styles.time}>
          <div className={styles.selectTime}></div>
          <img src={ArrowImg} />
        </div>
      </div>
      <div className={styles.schedule_footer}>
        <div className={styles.previuseBtn}></div>
        <div className={styles.nextBtn}></div>
      </div>
    </section>
  );
};

export default Schedule;
